import React from "react"
import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"




const MacetaRobot = () => {
    const data = useStaticQuery(graphql`
    query {
        robotMaceta1: file(relativePath: { eq: "robotMaceta1.jpg" }) {
        childImageSharp {
          fluid (quality: 100){
            ...GatsbyImageSharpFluid
          }
        }
      }
      robotMaceta2: file(relativePath: { eq: "robotMaceta2.jpg" }) {
        childImageSharp {
          fluid (quality: 100){
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
    return(
  <Layout>
    <SEO  
    title="Macetas robots y planta de papel" 
    meta="Monta un puzzle que crea una maceta con forma de robot. En el podrás poner plantas! Te explicamos como hacer plantas de papel para que las pongas encima del robot!"
    description="Monta un puzzle que crea una maceta con forma de robot. En el podrás poner plantas! Te explicamos como hacer plantas de papel para que las pongas encima del robot!"
    lang="es"
    />

    <div class="postContainer">
    <p>&nbsp;</p>
    <h1>Macetas robots y planta de papel</h1>
<p>En este post te vamos a hablar sobre unas macetas muy originales y una planta hecha de cartulianas que le encantarina a tu amiga.</p>
<p>
</p>
<div class="row">
  <div class="column">
  <Img fluid={data.robotMaceta1.childImageSharp.fluid} />
  </div>
  <div class="column">
  <Img fluid={data.robotMaceta2.childImageSharp.fluid} />
  </div>
</div>
<p>
</p>
<p>Las macetas que te proponemos son unos puzzles 3D que al montarlos crean un robot, con posibilidad de colocar una maceta en su parte superior. Creemos que es un regalo muy original ¿Donde se ha visto un puzzle al que le puedas plantar plantas?
    
    </p><p> El robot se lo puedes regalar sin montarlo,si no se lo vas a regalar con la planta. Si se lo regalas con la planta te recomendamos que montes tu misma el robot y se lo regales ya con la planta. Seria raro regalarle el robot por montar y la planta. Si se lo regalas todo junto y montado le impactara más. Esta compañía realiza diferentes estilos de robot puzzle deberás elegir el que más te guste.Estos puzzles son creados por una empresa Inglesa que realiza estos puzzles y otros muchos más, aquí te dejamos el <a href="https://www.robotimeonline.com/collections/flower-pot-1">enlace.</a></p>
<p>
</p>
<p>Si crees que seria un engorro para tu amiga colocar una planta de verdad en el robot, ya que tendría que regalarla periódicamente y cambiarle la tierra, te damos ideas de como crear plantas con materiales sencillos y realmente bonitas.</p>
<br></br>
<br></br>
<h2>Cactus de papel</h2>
<p>Los cactus de papel son muy fáciles de hacer. Solo necesitas cartulinas unas tijeras y pegamento liquido. El cactus encima de uno de estos robots quedan muy bien.</p>
<p>
</p>
<p><iframe class="video" title="1000" src="https://www.youtube.com/embed/zTzMtJdRsK4" ></iframe></p>
<br></br>
<br></br>
<h2>Flores de papel</h2>
<p>Las flores de papel son más complejas de hacer que lo cactus que te hemos mencionado anteriormente. Se necesitan más materiales como cinta de florería y alambre. Dan más juego que los cactus por que puedes crear flores de distintos colores y formas. Quedan mejor que los cactus pero vas a tardar más en hacerlas.</p>
<p><iframe class="video" title="1001" src="https://www.youtube.com/embed/yhCgdAt6lko" data-mce-fragment="1"></iframe></p>
<p>Si quieres hacer una maceta realmente bonita puedes mezclar flores de distintos colores y formas junto con un cactus. Esto le dará mas viveza a la maceta y junto al robot será un regalo espectacular, un regalo único.</p>
</div>
    <div style={{ height:`300px`}}>
    </div>
  </Layout>
    )
    }

export default MacetaRobot
